.headingContainer{
    background-color: rgb(182, 167, 167);
    width: 100%;
    justify-content: space-between;
    display: flex;
    border-radius: 8px;

    
}
.headingContainer img{
    width: 60px;
    height: 60px;
    padding: 8px;
    margin: 0;
    border-radius: 20px;
}

.headingContainer h3{
    font-family: 'Poppins';
    margin-right: 1000px;
    align-items: center;
    display: flex;
    }

    .headingContainer h3:hover{
        transform: scale(.96) ;
        cursor: pointer;
        color: chartreuse;
    }

    .headingContainer img:hover{
        transform: scale(.96) ;
        cursor: pointer;
    }

.headingContainer .navItems li{
    list-style: none;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 20px;
    cursor: pointer;
    padding-right:40px ;
     
}

.headingContainer .navItems li:hover{
    /* transform: scale(1.2); */
    color: rgb(17, 116, 43);
}

.navItems{

    display: flex;
    align-items: center;

}
.appBody{
    display: flex;
   
    
}

.searchInp{
    margin: 10px;
    width: 450px;
    height: 50px;
    border-radius: 5px;
    margin-right: 5px;
    border: none;
    background-color: rgb(232, 241, 249);
    font-size: 22px;
}

.searchInp::placeholder{
    font-family: 'Poppins';

    font-size: 20px;
}

.searchBtn{
    margin: 10px;
    width: 100px;
    font-size: 19px;
    font-family: 'Poppins', sans-serif;
    border-radius: 5px;
    border: none;
    background-color: rgb(215, 180, 134);
    cursor: pointer;

}
.rest1img{
    width: 350px;
    height: 250px;
    margin: 30px;
    border-radius: 10px;
}

.restaurantInfo{
   
    font-family: 'Poppins';
    
}

.restaurantInfo h2{
    margin-top: -8px;
    padding-left: 35px;
   
}

.restaurantInfo h3{
    margin-top: -15px;
    padding-left: 35px;
}
.restaurantInfo h4{
    margin-top: -15px;
    padding-left: 35px;
    color: rgb(152, 152, 147);
}

.resBody{
    display: flex;
    flex-wrap: wrap;
}

.rest_card:hover{
    transform: scale(.96) ;
    cursor: pointer;
   
}